export default {
	data() {
		return {
			height: '',
			orderno: '',
			orderInfor: [],
			merchantId: '',
			tg:'',
			refuseToReason:'',
		}
	},
	onLoad(e) {
		let that = this;
		uni.getStorage({
			key: 'userinfo',
			success(e) {
				that.merchantId = e.data.id;
			}
		});
		that.orderno = e.orderno;
		that.loadInfor();
	},
	onReady() {
		this.getTopheight();
	},
	methods: {
		getTopheight: function() {
			let that = this;
			uni.getSystemInfo({
				success(res) {
					that.height = res.statusBarHeight + 44 + "px";
				}
			})
		},
		loadInfor: function() {
			let that = this;
			uni.request({
				url: that.serveipd + "/api/merchant/order/refund",
				method: 'GET',
				data: {
					orderno: that.orderno,
				},
				success(res) {
					that.orderInfor.push(res.data.data);
					console.log(that.orderInfor);
				}
			});
		},
		agree: function() {
			let that = this;
			uni.request({
				url: that.serveipd + "/api/merchant/order/agreeToRefund",
				method: "GET",
				data: {
					orderno: that.orderno
				},
				success(res) {
					console.log(res);
					uni.showToast({
						title: '退款成功',
						mask: false,
						duration: 2000,
						success() {
							setTimeout(function() {
								uni.navigateTo({
									url:"./refund"
								})
							}, 2000);

						}
					});

				}
			})
		},
		// 取消
		cancel:function(){
			this.tg=false;
			this.refuseToReason='';
		},
		// 确定拒绝
		determine:function () {
			this.tg=false;
			this.refuse();
		},
		refuse: function() {
			let that = this;
			uni.request({
				url: that.serveipd + "/api/merchant/order/merchantRefusedToRefund",
				method: "GET",
				data: {
					orderno: that.orderno,
					merchantId: that.merchantId,
					refuseToReason:that.refuseToReason,
				},
				success(res) {
					uni.showToast({
						title: '已拒绝退款',
						mask: false,
						duration: 2000,
						success() {
							setTimeout(function() {
									uni.navigateTo({
									url:"./refund"
								})
							}, 2000);

						}
					});
				}
			})
		},
	}
}
